<script>
import { mapState } from 'vuex';
export default {
  name: 'profile',
  data(){
      return{
          isShowingMenu: false, 
          inDashBoard:true,
          inOrders:false,
          inProducts:false,
          numberr:20,
          hasScrolled:false,
          inProfile:false,
      }  
  },   
    computed:{
        hasChanceToScroll:function(){
            if(this.inDashBoard === true){
                return false
            }
            return true
        },
        ...mapState({
            user:'auth',
        }),
    },  
    methods: {
        switchProfile:function(){
            this.inProfile = true
        },
        onScroll:function(){
            if(this.hasScrolled === false /*&& this.isShowingMenu === false*/){
                this.hasScrolled = true
                this.isShowingMenu = false
            }
        },
        setHasScrolledFalse:function(){
            this.hasScrolled = false
        },
        showMobileMenu:function(){
            this.isShowingMenu = true
        },
        hideMobileMenu:function(){
            this.isShowingMenu = false
        },
        showDash:function(){
            this.inDashBoard = true
            this.inOrders = false
            this.inProducts = false

            this.setHasScrolledFalse()
        },
        showOrders:function(){
            this.inDashBoard = false
            this.inOrders = true
            this.inProducts = false
            
            this.setHasScrolledFalse()
            
        },
        showProd:function(){
            this.inDashBoard = false
            this.inOrders = false
            this.inProducts = true

            this.setHasScrolledFalse()
        }
    },  
}
</script>
